// global styles
html{
  background: url("/assets/images/gradientbg.jpg") no-repeat center;
  background-attachment: fixed;
  background-size: cover;
  font: normal 18px "Roboto", Helvetica, sans-serif;
}

//index styles

.home-grid-wrapper{
	max-width: 1000px;
	margin: 1em auto;
	padding: 1em;
	display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 1em;
	overflow: hidden;

	h1{
		grid-column: 1 / span 3;
    grid-row: 1;
		visibility: hidden;
	}

	h2.intro{
		width: 1em;
		grid-column: 1;
		grid-row: 1 / span 3;
		writing-mode: vertical-rl;
		align-self: end;
		justify-self: center;
		color: white;
		font-size: 3em;
		font-weight: 300;

	}

	h2.intro-name{
		grid-column: 2 / span 2;
    grid-row: 3;
    align-self: end;
		color: white;
    font-family: 'Rozha One', serif;
    font-size: 7em;
    margin-bottom: -.2em;
	}

	p{
		grid-column: 1 / span 3;
		grid-row: 4 / span 3;
		align-self: center;
		color: white;
		font-size: 1.25em;
		font-weight: 300;
		line-height: 1.5em;
	}

	.line-1{
		display: block;
		background-color: white;
		height: 100%;
		width: .5em;
		grid-column: 1;
		grid-row: 1 / span 3;
		z-index: -1
	}

	.shape-1{
		display: block;
    height: 15em;
    width: 15em;
    grid-column: 2 / span 2;
    grid-row: 2 / span 2;
    border-radius: 100%;
    align-self: end;
    justify-self: right;
    border: 3px solid rgba(255, 255, 255, .5);
    z-index: -1;
    margin-right: -4em;
	}
	.shape-2{
		display: none;
	}
}

@media (min-width: 375px){
	.home-grid-wrapper{
		h2.intro-name{
			font-size: 9em;
		}
	}
}

@media (min-width: 425px){
	.home-grid-wrapper{
	  grid-template-rows: 1fr 1fr 1fr 1fr;
		h2.intro, .line-1{
			grid-row: 1 / span 2;
		}
		h2.intro-name{
			grid-row: 2;
			font-size: 10em;
		}
		p{
			grid-row: 3 / span 2;
		}
		.shape-1{
			grid-row: 1 / span 2;
			height: 20em;
	    width: 20em;
		}
	}
}

@media (min-width: 600px){
	.home-grid-wrapper{
		padding: 3em;
	  grid-template-columns: 1fr 1fr 1fr 1fr;
		.shape-2{
			display: block;
	    height: 4em;
	    width: 4em;
	    grid-column: 4;
	    grid-row: 3;
	    border-radius: 100%;
	    align-self: start;
	    justify-self: left;
	    border: 3px solid rgba(255, 255, 255, .5);
	    z-index: -1;
		}
	}
}

@media (min-width: 768px){
	.home-grid-wrapper{
		padding: 5em;
	}
}

@media (min-width: 900px){
	.home-grid-wrapper{
		padding: 6em;
	}
}

